*{
    padding: 0;
    margin: 0;
}

.products{
    padding:20px 30px;
    font-family: sans-serif;
    width: 100%;
}
.products .container{
    padding: 10px 10px;
    max-width: 100%;
    display: flex;
    justify-content: space-between;
}

.products .container .filter{
    padding: 10px 5px;
}
.products .container .filter .categories h3{
    text-transform: uppercase;
    font-size: 28px;
    font-weight:600;
}
.products .container .filter .categories ul{
    padding: 10px 10px;
}
.products .container .filter .categories li{
    list-style: none;
    cursor: pointer;
    margin-top: 10px;
    color: royalblue;
    font-size:20px;
    padding-left:10px;
}
.products .container .productbox{
    margin-right: 10%;
}
.products .container .productbox .items{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.products .container .productbox .items .box{
    overflow: hidden;
    width: 280px;
    height: 380px;
    border: 1px solid #e2e0e0;
    margin-bottom: 20px;
    cursor: pointer;
    border-radius: 5px;
    transition: 0.3s;
}
.products .container .productbox .items .box:hover{
    box-shadow: rgba(0,0,0,0.34) 3px 3px 2.6px;
}
.products .container .productbox .box .img_box{
    display: flex;
    justify-content: space-between;
    border: 1px solid #e2e0e0;
    align-items: center;
    justify-content: center;
    
}
.products .container .productbox .items .box .img_box img{
    width: 200px;
    height: 250px;
    margin-right:10px;
    border-radius:10px;
}
.products .container .productbox .items .box .img_box .icon{
    z-index: 1;
    overflow: hidden;
    position: relative;
    right: 0;
    display: flex;
    flex-direction: column;
    transition: 0.3s;
    margin-top: 20px;
}
.products .container .productbox .items .box:hover .img_box .icon{
    transform: translateX(-130px);
}
.products .container .productbox .items .box .img_box .icon li{
    list-style: none;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    background: #fff;
    font-size: 18px;
    transition: 0.3s;
   
}
.products .container .productbox .items .box .img_box .icon li:hover{
    background: royalblue;
    color: #fff;
}
.products .container .productbox .items .box .details{
    margin-left: 20px;
    margin-top: 15px;
    margin-bottom: 10px;
}
.products .container .productbox .items .box .details p{
    color: #c1bebe;
    padding-bottom: 8px;
}
.products .container .productbox .items .box .details h3{
    color: black;
    padding-bottom: 5px;
}
.products .container .productbox .items .box .details h4{
    color: cornflowerblue;
}
.productdetails
{
    margin-top: 30px;
    padding: 30px 40px;
    width:75%;
    height:75%;
    position: fixed;
    z-index: 2;
    margin-top: -20%;
    background: rgba(0, 0, 0, 0.24);
}
.productdetails .container
{
    margin-top: 7%;
    margin-left: 8%;
    width: 1000px;
    height: 550px;
    background: #fff;
    padding: 20px 30px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.productdetails .container .closebtn
{
    margin-left: 100%;
    border: none;
    outline: none;
    background: none;
    font-size: 32px;
    cursor: pointer;
}
.productdetails .container .productbox
{
    max-width: 100%;
    display: flex;
    padding: 20px 30px;
    margin-top: 20px;
    justify-content: space-between;
}
.productdetails .container .productbox .img_box
{
    padding: 20px 30px;
    width: 300px;
    height: 300px;
    background: #f1f1f1;
    
}
.productdetails .container .productbox .img_box img
{
    width:300px;
    height:300px;
}
.productdetails .container .productbox .details
{
    margin-left: 30px;
    margin-top: 10px;
}
.productdetails .container .productbox .details h4
{
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 200;
    color: #9a9a9a;
    letter-spacing: 1px;
}
.productdetails .container .productbox .details h2
{
    margin-top: 20px;
    font-size: 32px;
    color: #010f1c;
    text-transform: capitalize;
    letter-spacing: 1px;
}
.productdetails .container .productbox .details p
{
    margin-top: 10px;
    color: #4a4a4a;
}
.productdetails .container .productbox .details h3
{
    margin-top: 10px;
    color: #010f1c;
    font-size: 32px;
    letter-spacing: 1px;
}
.productdetails .container .productbox .details button
{
    margin-top: 30px;
    padding: 15px 60px;
    font-size: 16px;
    color: #fff;
    background: #010f1c;
    transition: 0.5s;
    border: none;
    outline: none;
    cursor: pointer;
}
.productdetails .container .productbox .details button:hover
{
    background: #0989ff;
}