*{
  
  margin: 0;
  padding:0;
  font-family: sans-serif;
  box-sizing: border-box;
}

.footer{
    padding: 30px 40px;
    width: 100%;
    background: #ecf2f7;
}
.footer .container{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
}
.footer .container .about{
    max-width: 30%;
   
}
.footer .container .about .detail p{
  margin-top: 20px;
  font-size:18px;
  font-family: 'Times New Roman', Times, serif;
}
.footer .container .about .detail .icon{
  display: flex;
  margin-top: 20px;
}
.footer .container .about .detail .icon li{
  list-style: none;
  margin-left: 20px;
  padding: 10px 10px;
  font-size: 25px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  cursor: pointer;
  transition: 0.2s;
}
.footer .container .about .detail .icon li:hover{
    background:#002939 ;
    color: #fff;
}
.footer .container .account{
margin-right:110px;
margin-top: 20px;
}
.footer .container .account h3{
  font-size: 32px;
}
.footer .container .account ul{
  margin-left: 20px;
  margin-top: 15px;
  font-size: 18px;
  transition: 0.2s;
  cursor: pointer;}
 
  .footer .container .account ul li{
  list-style:none;
}

.footer .container .pages{
  margin-right:110px;
  margin-top: 20px;
  }
  .footer .container .pages h3{
    font-size: 32px;
  }
  .footer .container .pages ul{
    margin-left: 20px;
    margin-top: 15px;
    font-size: 18px;
    transition: 0.2s;
    cursor: pointer;
    list-style: none;
  }