*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

.top_banner{
    padding: 30px 40px;
    width: 100%;
    background: #e3edf6;
}
.top_banner .container{
    display: flex;
    max-width: 100%;
    justify-content: space-between;
}
.top_banner .container .detail{
    margin-top: 8%;
    margin-left: 8%;
}
.top_banner .container .detail h2{
    font-size: 52px;
    color: #010f1c;
    margin-right: 40%;
    margin-bottom: 20px;

}
.top_banner .container .img_box img{
    object-fit:fill;
    height: 390px;width: 370px;
}
.about{
    padding:30px 40px;
    width: 100%;
}
.about .container{
    max-width: 100%;
    display: flex;
    justify-content: space-between;

}
.about .container .box{
    padding: 20px 20px;
    display: flex;
    background: #ecf2f7;
    width:300px;
    cursor: pointer;
    border-radius: 3px;
    height: 90px;
    
}
.about .container .box .icon{
    color:rgb(249, 148, 165);
    font-size: 32px;

}
.about .container .box .details{
    margin-top: 10px;

}
.about .container .box .details h3{
color: black;
}
.about .container .box .details p{
    color:#ffffffed;
}

.link{
    text-decoration: none;
    color: #010f1c;
    padding: 10px 20px;
    border: 10px;
    background: #fff;
    justify-content: space-between;
    transition: 0.2s;
    margin-left: 50px;
    border-radius: 10px;
}
.link:hover{
    background: #0989ff;
    color: #fff;

}
.product_type{
    padding: 20px 30px;
    width: 100%;
    
}
.product_type .container{
    display: flex;
    padding: 20px 30px;
    justify-content: space-between;
    max-width: 100%;
    
}

.product_type .container .box{
    padding: 10px 10px;
   margin-left:5px ;overflow: hidden;
    
}
.product_type .container .box .img_box{
    height: 200px;
    width: 200px;
    border-radius: 50%;
    background: #def7eb;
    
}
.product_type .container .box .detail{
    max-width: 100%;
    align-items: center;
    text-align: center;
}
.product_type .container .box .detail p{
    margin-top: 10px;
    color: grey;
}

img{
height: 100px;
width: 100px;
margin-top: 40px;
margin-left: 45px;
transition: 0.3s;
align-items: center;
border-radius: 30%;
}
.product_type .container .box .img_box:hover img{
    transform: scale(1.7);
}


.product{
    padding:10px 20px;
    width: 100%;
}
.product .container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    
}
.product .container .box{
    display: flex;
    height: 350px;
    width: 300px;
    justify-content: space-between;
    overflow: hidden;
    border: 1px solid #cbcbcb;
    margin-bottom: 20px;
    transform: 0.5s;
    cursor: pointer;
    border-radius: 5px;
}

.product .container .box:hover{
   box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}


.product .container .box .img_box img{
    height: 230px;
    width: 240px;
    padding-bottom:8px;
    
}

.product .container .box .img_box .icon{
    position:absolute;
    width:2.5%;
    display: flex;
    flex-direction: column;
    transform: translateY(-272px);  
}

li:hover{
   color: #0989ff;
}
.product .container .box .img_box .icon li{
    padding: 10px 10px;
    list-style: none;
    font-size:18px;
    cursor: pointer;
    box-shadow: #a5a5a5 0px 3px 8px;
    transition: 0.1s;
    background: #fff;
}
.product .container .details{
    position:absolute;
    margin-top: 265px;
    margin-left: 20px;
    border-top: 1px solid #010f1c;

}

.product .container .details p{
    color: grey;
    font-size: 14px;
}
.product .container .details h3{
    font-size: 16px;
    color: #010f1c;
    transition: 0.5s;
    margin-top: 5px;
}
.product .container .details h3:hover{
    color:#0989ff;
}

.product .container .details h4{
    margin-top: 5px;
    color: #0989ff;
    letter-spacing: 1px;
    font-size: 14px;
}
.banner{
    padding: 10px 20px;
    width: 100%;
}
.banner .container{
    padding: 30px 40px;
    background: #75B7BA;
    justify-content: space-between;
    display: flex;
    border-radius: 5px;
}
.banner .container .detail{
    margin-top: 90px;
    padding-left:80px;
   
}
.banner .container .detail h3{
    color:#fff;
    font-size: 31px;
}
.banner .container .detail h4{
    font-size: 28px;
    padding-top: 20px;
    font-family: 'Times New Roman', Times, serif;
    margin-bottom: 30px;
}
.banner .container .detail link{
    margin-left: 150px;
    border-radius: 10px;
}
.banner .container .img_box{

    padding-left: 40px;
    margin-right: 200px;
}
.banner .container .img_box img{
    height: 420px;
    width: 390px;
    border-radius: 10px;
}
