*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

.free{
    padding:10px 30px;
    width: 100%;
    display: flex;
    background: #010f1c;


}

.free-icon{
    color:orange;
    font-size: 32px;
}
.free p{
font-size: 16px;
color: white;
font-weight: 500;
margin-left: 10px;
margin-top: 8px;
}

.main-header{
    padding: 10px 10px;
    border-bottom:1px solid #010f1c;
    width: 100%;

}

.main-header .container{
    display: flex;
    justify-content:space-between;
    align-items: center;
    
}

.main-header .container .search-box{
margin-left: 30px;
}
.main-header .container .search-box input{
    outline: none;
    padding: 10px 30px;
    border: 2px solid #002939;
    width: 300px;
    color: black;
}
.main-header .container .search-box button{
border: 1px solid #002939;
outline: none;
padding: 11px 30px;
background: #002939;
color: #ecf2f7;
}
.logo img{
    height: 50px;
    width: 150px;
}

.main-header .container .icons{
    display: flex;
    margin-right: 50px;
}
.main-header .container .icons .account {
    display: flex;
    margin-right: 30px;
    font-size: 22px;
}
.main-header .container .icons .account .account-user{
    margin-right: 10px;
    font-size: 32px;
    color:black;
    cursor: pointer;
    
}
.main-header .container .icons .account p{
    margin-right: 5px;
    margin-top: 2px;
}
 .other-icons{
    font-size: 32px;
    justify-content: space-between;
}

.header{
    max-width: 100%;
    padding: 20px 30px;
    display: flex;
    box-shadow: #979999 0px 5px 15px;
}

.header .container{
    display: flex;
    justify-content: space-between;
    max-width: 100%;
}
.header .container .nav{
    display: flex;
    width: 100%;
}
.header .container .nav ul
{
    display:flex;
}
.header .container .nav ul li
{
    list-style: none;
    margin-left: 20px;
}
.link
{
    text-decoration: none;
    transition: 0.5s;
    color: #010f1c;
}
 .link:hover{
    color:#002939;
}

.header .container .auth{
    font-size: 32px;
    margin-left: 650px;
    display:flex;
}
.header .container .auth button{
    background: none;
    border: none;
    justify-content: space-between;
    font-size: 22px;
}